body {
  margin: 0;
  padding: 0;
}

html {
  background: url("background.7240b565.webp") center / cover no-repeat fixed;
}

* {
  font-family: audiowideregular;
}

center {
  margin: 0 auto;
}

.center {
  text-align: center;
}

.tint {
  z-index: 1;
  height: 100%;
  width: 100%;
  background: linear-gradient(#000000b3, #1d1d1d);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

@font-face {
  font-family: audiowideregular;
  src: url("font_latin-webfont.724c3a88.woff2") format("woff2"), url("font_latin-webfont.4347aa63.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.header {
  filter: drop-shadow(5px 5px 5px #222);
  width: 600px;
}

ul {
  margin: 0;
  padding: 0 20px;
  list-style-type: none;
  display: table;
  overflow: hidden;
}

ul.list {
  list-style-type: disc;
}

.project {
  color: #fff;
  background-color: #0000004d;
  border-top: 7px solid #444;
  border-bottom: 7px solid #f4ba6e;
  margin: 12px;
  padding: 8px 12px 29px;
  font-size: 16px;
  box-shadow: 0 2px 9px #0006;
}

ul.boxes {
  display: table;
}

div.box {
  width: 100%;
  color: #fff;
  background-color: #2a2a2a;
  border-bottom: 7px solid #444;
  margin: 7px 7px 7px -6px;
  padding: 5px;
  font-size: 16px;
  display: table;
  box-shadow: 0 2px 9px #0006;
}

div.box img {
  margin: 10px;
}

img {
  border-radius: 8px;
}

h1 {
  text-align: left;
  color: #f08080;
  font-size: 24px;
}

h2 {
  font-size: 16px;
}

a.button {
  text-align: center;
  cursor: pointer;
  color: #fff;
  background-color: #222;
  border: none;
  border-bottom: 4px solid #444;
  margin: 10px 10px 10px 0;
  padding: 14px;
  font-size: 16px;
  text-decoration: none;
  transition-duration: .3s;
}

a.icon span {
  padding-top: 2px;
  padding-bottom: 30px;
  padding-left: 36px;
  background-repeat: no-repeat !important;
  background-size: 28px !important;
}

a.button:hover {
  background-color: gray;
  box-shadow: 0 0 16px #0006;
}

.top {
  width: 0%;
  height: 0%;
  position: relative;
  top: 100px;
  left: 0;
}

.zoomer {
  cursor: pointer;
  max-height: 300px;
  width: auto;
  height: auto;
  transition: all .2s;
}

.zoomer:hover {
  opacity: .8;
}

.modal {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #000000e6;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.modal-content {
  max-width: 80%;
  max-height: 90%;
  width: auto;
  height: auto;
  border-radius: 16px;
  margin: auto;
  animation-name: zoom;
  animation-duration: .6s;
  display: block;
  position: absolute;
  inset: 0;
}

@keyframes zoom {
  from {
    transform: scale(0);
  }

  to {
    transform: scale(1);
  }
}

.close {
  color: #f1f1f1;
  font-size: 70px;
  font-weight: bold;
  transition: all .3s;
  position: absolute;
  top: 25px;
  right: 45px;
}

.close:hover, .close:focus {
  color: coral;
  cursor: pointer;
  text-decoration: none;
}

